.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100vw;
}

.dice-container {
  background-image: url("../public/images/container.png");
  background-size: contain;
  background-repeat: no-repeat;
  height: 200px;
  width:500px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin:-3px;
}

.dice-image {
  width: 50px;
  margin: 0 10px;
}

.roll-button-container {
  background-image: url("../public/images/button.png");
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px; /* adjust to match dice-container height */
  margin: 0 0px;
  transition: transform 0.3s ease; /* add transition property */
}

.roll-button-container:hover {
  transform: translateY(5px); /* move background down on hover */
}

.roll-button {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
  outline: none;
  min-width: 300px;
  height: 100%; /* make button element fill the height of the container */
}
